<template>
  <v-dialog v-if="show" v-model="show" :full-width="false" :persistent="true">
    <!--user v-if here so that the component gets properly destroyed along with user/pass info-->
    <login :suppressRouting="true" @loginsuccess="loginSuccess"></login>
  </v-dialog>
</template>
<script>
  import Login from '@/components/auth/Login'
  export default {
    components: {
      Login
    },
    props: {
      show: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      showLogin () {
        return this.show
      }
    },
    methods: {
      loginSuccess (val) {
        console.log('Login success detected in dialog', val)
        if (val) {
          this.$emit('loginsuccess', Boolean(val)) // emit up to parent of the dialog
        }
      }
    }
  }
</script>



