<template>
  <div>
    <login-dialog :show="showLoginDialog" @loginsuccess="loginSuccess"/>
    <router-view></router-view>
  </div>

</template>
<script>
  import {eventbus} from '@/assets/js/eventbus'
  import LoginDialog from '@/components/auth/LoginDialog'

  export default {
    components: {
      LoginDialog
    },
    data () {
      return {
        showLoginDialog: false
      }
    },
    methods: {
      loginSuccess: function (val) {
        this.showLoginDialog = !val // hide if successful
        console.log('Parent heard the event!', val)
      }
    },
    watch: {
      showLoginDialog () {
        console.log('Show dialog changed:', this.showLoginDialog)
      }
    },
    created: function () {
      eventbus.$on('errors:401', error => {
        console.log('401 ERROR handling in Authorized View', error)
        this.showLoginDialog = true
      })
      eventbus.$on('errors:422', error => {
        console.log('422 ERROR handling in Authorized View', error)
        this.showLoginDialog = true
      })
    }
  }
</script>



