<template>
  <div>
    <v-card>
      <v-card-text>
        <p class="text-xs-center">
          <span class="display-1 primary--text">Login</span>
        </p>
        <v-form v-model="formValid" ref="form" @submit.prevent="submitForm()" lazy-validation>
          <v-layout row>
            <v-flex>
              <v-text-field
                label="User"
                v-model="username"
                :rules="usernameRules"
                type="input"
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex>
              <v-text-field
                label="password"
                v-model="password"
                :rules="passwordRules"
                type="password"
              ></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row justify-center>
            <v-flex text-xs-center>
              <v-btn
                outline
                color="primary"
                :disabled="!formValid"
                type="submit"
              >Login
              </v-btn>
            </v-flex>
          </v-layout>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
  import authservice from '@/services/authservice'
  export default {
    name: 'Login',
    props: {
      suppressRouting: {
        type: Boolean,
        default: false
      }
    },
    data: function () {
      return {
        username: null,
        usernameRules: [
          (v) => !!v || 'Username is required'
        ],
        password: null,
        passwordRules: [
          (v) => !!v || 'Password is required'
        ],
        formValid: false
      }
    },
    methods: {
      submitForm: function () {
        if (this.formValid) {
          authservice.login(this.username, this.password)
            .then(response => { this.handleSuccess(response) })
            .catch(error => {
              console.log('LOGIN ERROR:', error)
            })
        }
      },
      handleSuccess: function (response) {
        console.log('login success!', response || 'No message')
        if (!this.suppressRouting) {
          if (this.$route.query.hasOwnProperty('redirect')) {
            this.$router.push(this.$route.query.redirect)
          } else {
            this.$router.push({name: 'Admin'})
          }
        }
        this.$emit('loginsuccess', Boolean(true))
      }
    }
  }
</script>



